import React, { Component } from "react";
import { StyledContainer } from './container';

import apple from "./images/apple.png";
import shuffleArray from "./shuffle";

let gameAns;
let gameStat;
class Hangman extends Component {
	static defaultProps = {
		maxWrong: 5,
		originalImages: [apple, apple, apple, apple],
		imageSizes: ['responsive apple1', 'responsive apple2', 'responsive apple3', 'responsive apple4']
	};

	constructor(props) {
		super(props);
		this.state = {
			filled: 0,
			answer: [],
			order: ['ascending','descending'],
			shuffledImageSizes: shuffleArray(this.props.imageSizes)
		};
		this.handleClick = this.handleClick.bind(this);
	}

	handleClick = (val) => {
		// access to e.target here
		this.setState({
			filled: this.state.filled + 1,
			answer: this.state.answer.concat(val) // [...st.answer, val]
			// https://stackoverflow.com/questions/38558200/react-setstate-not-updating-immediately
		});
		console.log(this.state.answer);
		console.log(val);
	}

	isEqual = (arr1, arr2) => {
		if (arr1.length !== arr2.length) {
			return false; 
		} else {
			{ 
				// comparing each element of array 
				 for (var i = 0; i < arr1.length; i++) {
					 if (arr1[i] !== arr2[i]) {
						 return false; 
					 }
				 return true; 
				} 
			 }
		}
	}

	generateContainers() {
		return this.state.shuffledImageSizes.slice().map(idx => (
				<StyledContainer maxWidth="sm">
					<img src={this.props.originalImages[0]} className='responsive apple1' alt={1} />
				</StyledContainer>
			))
	}

	generateImages() {
		return this.state.shuffledImageSizes.slice().map(imageSize => (
			<li style={{"display": "inline"}}>
				<button>
					<img 
						key={[...Array(this.state.shuffledImageSizes.length).keys()]} 
						src={this.props.originalImages[0]} 
						className={imageSize} 
						alt={imageSize}
						onClick={((e) => this.handleClick(e.currentTarget.className))}
					/>
				</button>
			</li>
		));
	}

	endingMessage(result) {
		if (result === 'win') {
			return (
				<h1>YOU WON</h1>
			)
		} else {
			return (
				<h1>YOU LOST</h1>
			)
		}
	}

	resetButton = () => {
		this.setState({
			filled: 0,
			shuffledImageSizes: shuffleArray(this.props.imageSizes),
			answer: []
		});
	};

	render() {
		const isWinner = this.isEqual(this.state.answer, this.props.imageSizes);
		gameAns = this.generateContainers();
		gameStat = this.generateImages();
		if (this.state.filled === this.props.maxWrong) {
			if (isWinner) {
				gameStat = this.endingMessage('win');
			} else {
				gameStat = this.endingMessage('lose');
			}
		}

		return (
			<div className='Hangman'>
				<nav className='navbar navbar-expand-lg'>
					<a className='navbar-brand text-dark fontSpace' href='/'>
						Sorting Game
					</a>
					<span className='d-xl-none d-lg-none text-dark fontSpace'>
						Filled: {this.state.filled}
					</span>
					<button
						className='navbar-toggler sr-only'
						type='button'
						data-toggle='collapse'
						data-target='#navbarText'
						aria-controls='navbarText'
						aria-expanded='false'
						aria-label='Toggle navigation'
					>
						<span className='navbar-toggler-icon'></span>
					</button>
					<div className='collapse navbar-collapse' id='navbarText'>
						<ul className='navbar-nav mr-auto'>
							<li className='nav-item '></li>
							<li className='nav-item'></li>
							<li className='nav-item'></li>
						</ul>
						<span className='navbar-text text-dark fontSpace'>Filled: {this.state.filled}</span>
					</div>
				</nav>
				<div className='container center'>
					<p className='text-center instructions'>Arrange in {this.state.order[0]} order as shown below</p>
				</div>
				<div className='imgArray text-center container'>
					<ul>
						<li><img src={this.props.originalImages[0]} className='responsive apple1' alt={1} /></li>
						<li><img src={this.props.originalImages[0]} className='responsive apple2' alt={2} /></li>
						<li><img src={this.props.originalImages[0]} className='responsive apple3' alt={3} /></li>
						<li><img src={this.props.originalImages[0]} className='responsive apple4' alt={4} /></li>
					</ul>
				</div>
				<div>
					{gameAns}
				</div>
				<div className='Hangman2 imgArray text-center container'>
					{gameStat}
				</div>
				<div>
					<button className='Hangman-reset' onClick={this.resetButton}>
						Reset
					</button>
				</div>
			</div>
		);
	}
}

export default Hangman;